A,QAAA,CAGF,QACE,oBAAA,CACA,wBAAA","sourcesContent":["/* =======================\n   Reset & Variables\n======================= */\n* {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n}\n\n:root {\n  --light-color: #f4f4f6;\n  --dark-color: #333;\n  --main-color: #777;\n}\n\n/* =======================\n   Global\n======================= */\nhtml {\n  height: 100%;\n  scroll-behavior: smooth;\n}\n\nbody {\n  font-family: \"Lato\", sans-serif;\n  color: var(--main-color);\n  line-height: 1.8;\n  height: 100%;\n}\n\na {\n  text-decoration: none;\n}\n\nh1, h3 {\n  scroll-margin-top: 80px;\n}\n\n/* Animations */\n@keyframes opac {\n  from { opacity: 0; }\n  to { opacity: 1; }\n}\n\n@keyframes fadeEffect {\n  from { opacity: 0.4; }\n  to { opacity: 1; }\n}\n\n/* Create a Parallax Effect */\n.bgimg-1,\n.bgimg-2,\n.bgimg-3,\n.bgimg-4 {\n  background-attachment: fixed;\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n}\n\n/* =======================\n   Navbar\n======================= */\n.my-top,\n.my-bottom {\n  position: fixed;\n  width: 100%;\n  z-index: 1;\n}\n\n.my-top {\n  top: 0;\n}\n\n.my-bottom {\n  bottom: 0;\n}\n\n.my-bar {\n  display: flex;\n  justify-content: flex-end;\n  align-items: center;\n  width: 100%;\n  padding: 18px 10px;\n  background-color: var(--light-color);\n  z-index: 99;\n  opacity: 0.8;\n  transition: 0.4s;\n}\n\n.my-bar a {\n  padding: 12px;\n  font-size: 20px;\n  line-height: 25px;\n  color: var(--main-color);\n  border-radius: 4px;\n}\n\n.my-bar a:hover {\n  color: #111;\n  background-color: lightblue;\n}\n\n.my-bar a.active {\n  background-color: rgb(155, 155, 155);\n  color: var(--light-color);\n}\n\n@media (max-width: 768px) {\n  .my-bar a {\n    font-size: 14px;\n    padding: 8px;\n  }\n}\n\n/* =======================\n   Title Section\n======================= */\n/* First image */\n.bgimg-1 {\n  background-image: url('/assets/self3.png');\n  min-height: 100%;\n  opacity: 0.75;\n}\n\n.first-section {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  -ms-transform: translate(-50%, -50%);\n  text-align: center;\n  padding: 12px 24px;\n  color: #f4f4f6;\n  background-color: #333;\n  letter-spacing: 10px;\n  animation: opac 2s;\n}\n\n\n/* =======================\n   Trip Section\n======================= */\n.second-section {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  gap: 50px;\n  padding: 20px 10px;\n  margin: 20px auto;\n  scroll-margin-top: 80px;\n}\n\n.second-section .content {\n  max-width: 500px;\n}\n\n.second-title {\n  font-size: 24px;\n  text-align: center;\n  scroll-margin-top: 80px;\n}\n\n.trip-info {\n  text-align: left;\n  font-size: 20px;\n}\n\n/*-- Carousel --*/\n.carousel-container {\n  position: relative;\n  width: 600px;\n  height: 500px;\n  overflow: hidden;\n  border-radius: 8px;\n}\n\n.carou-slide {\n  display: none;\n}\n\n.carou-slide img {\n  width: 100%;\n  height: 500px;\n  border-radius: 8px;\n}\n\n.prev,\n.next {\n  cursor: pointer;\n  position: absolute;\n  top: 50%;\n  transform: translateY(-50%);\n  padding: 12px;\n  font-size: 24px;\n  font-weight: bold;\n  color: var(--light-color);\n  background: var(--main-color);\n  border-radius: 3px;\n}\n\n.prev:hover,\n.next:hover {\n  background: var(--dark-color);\n}\n\n.prev {\n  left: 10px;\n}\n\n.next {\n  right: 10px;\n}\n\n/* =======================\n   Photos Section\n======================= */\n.bgimg-3 {\n  background-image: url('/assets/img0.png');\n  min-height: 400px;\n}\n\n.third-section {\n  position: relative;\n  text-align: center;\n  padding: 12px 24px;\n  opacity: 0.67;\n  scroll-margin-top: 80px;\n}\n\n.third-title {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  -ms-transform: translate(-50%, -50%);\n  font-family: comic;\n  font-size: 40px;\n  font-weight: bold;\n  font-style: italic;\n  color: var(--dark-color);\n  letter-spacing: 10px;\n  animation: opac 2s;\n}\n\n/* Photo Grid*/\n.photo-container {\n  margin: 32px 20px 20px;\n  display: grid;\n  justify-content: center;\n  grid-template-columns: 300px 300px 300px;\n  gap: 24px;\n}\n\n.photo-container img {\n  width: 300px;\n  height: 300px;\n  object-fit: cover;\n  cursor: pointer;\n  border-radius: 4px;\n  transition: transform 0.3s ease, filter 0.3s ease;\n}\n\n.photo-container img:hover {\n  transform: scale(1.05);\n  filter: brightness(50%);\n}\n\n@media (max-width: 1280px) {\n  .second-section { flex-direction: column; gap: 20px; }\n  .carousel-container, .second-section img { max-width: 90%; height: auto; }\n  .photo-container {\n    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\n  }\n}\n\n.modal {\n  display: none;\n  position: fixed;\n  z-index: 9999;\n  padding-top: 80px;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n}\n\n.modal-content {\n  background-color: #fefefe;\n  margin: auto;\n  padding: 20px;\n  border-radius: 8px;\n  width: 80%;\n  max-width: 700px;\n  text-align: center;\n}\n\n.modal-img {\n  max-width: 100%;\n  height: auto;\n  border-radius: 6px;\n  margin-bottom: 15px;\n}\n\n#modal-caption {\n  font-size: 18px;\n  font-weight: bold;\n  color: var(--main-color);\n  margin-top: 10px;\n}\n\n.closeModal {\n  float: right;\n  font-size: 32px;\n  font-weight: bold;\n  cursor: pointer;\n  color: #333;\n}\n.closeModal:hover {\n  color: lightcoral;\n}\n\n/* =======================\n   Video Section\n======================= */\n.food {\n  display: flex;\n  margin: 80px auto;\n  flex-direction: column;\n  align-items: center;\n  gap: 50px;\n  scroll-margin-top: 80px;\n}\n\n.fourth-title {\n  width: 100%;\n  height: 100px;\n  background-color: var(--light-color);\n  font-size: 30px;\n  font-weight: bold;\n  color: var(--dark-color);\n  letter-spacing: 10px;\n  animation: opac 2s;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n#myvideo {\n  margin: 30px auto;\n  max-width: 600px;\n  width: 100%;\n  border-radius: 8px;\n}\n\n/* =======================\n   About Section\n======================= */\n.bgimg-4 {\n  background-image: url('/assets/self5.jpg');\n  min-height: 400px;\n  position: relative;\n  opacity: 0.8;\n  margin-top: 40px;\n  scroll-margin-top: 80px;\n}\n\n.bgimg-4 .about {\n  position: absolute;\n  top: 30%;\n  left: 30%;\n  transform: translate(-50%, -50%);\n  font-size: 45px;\n  font-weight: bold;\n  color: black;\n  z-index: 2;\n  font-family: fantasy;\n  animation: opac 2s;\n}\n\n.bgimg-4 .me {\n  position: absolute;\n  top: 70%;\n  left: 70%;\n  transform: translate(-50%, -50%);\n  font-size: 45px;\n  font-weight: bold;\n  color: black;\n  z-index: 2;\n  font-family: fantasy;\n  animation: opac 2s;\n}\n\n.content-aboutme {\n  margin: 30px;\n  padding: 0 20px 30px;\n  text-align: left;\n  font-size: 20px;\n}\n\n/* =======================\n   Footer\n======================= */\n.contact {\n  padding: 40px 20px;\n  text-align: center;\n  font-size: 20px;\n  color: var(--light-color);\n  background-color: var(--dark-color);\n}\n\n.contact .button {\n  color: var(--dark-color);\n  background-color: var(--light-color);\n  border-radius: 4px;\n  border: none;\n  cursor: pointer;\n  padding: 12px 20px;\n}\n\n.button:hover {\n  background-color: var(--main-color);\n}\n\n.contact .link {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  margin-top: 20px;\n  gap: 20px;\n}\n\n.link a {\n  text-decoration: none;\n  color: var(--light-color);\n}"],"sourceRoot":""}]);
